import './HelpPage.styl';

import React, {
  Component,
} from 'react';

import {
  Header,
  HeaderBottom,
  MenuHelp,
  HelpContent
} from 'components';

import PropType from 'prop-types';

const maxHeight = 167;

export default class HelpPage extends Component {
  static propTypes = {
    match: PropType.object
  };

  constructor(props) {
    super(props);

    this.state = {
      height: 0
    };
    this.scrollPosition = this.scrollPosition.bind(this);
  }

  componentWillMount() {
    window.addEventListener('scroll', this.scrollPosition);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.scrollPosition);
  }

  scrollPosition(e) {
    const height = e.srcElement.body.scrollTop;
    this.setState({ height });
  }

  render() {
    const path = this.props.match.path;
    const {
      height
    } = this.state;

    return (
      <div className="c-help-page">
        <div className="help-page-container">
          <Header
            indexPage={3}
          />
          <HeaderBottom
            smallTitle="SECTIONS"
            right={true}
            path={path}
            nav
            showSearchSmall
          />
          <div className="help-body-container">
            <div
              className={`help-left-container ${ height >= maxHeight && 'fixed-container' }`}
            >
              <MenuHelp
                path={path}
              />
            </div>
            <div className="help-right-container">
              <HelpContent
                path={path}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}



